<template>
  <main class="patientCare">
    <!-- <MStrongPopup
      :popupEventFlag="mStrongPopupEventFlag"
      @popupClose="toggleMStrongPopup"
    /> -->
    <CommonPatientPopup
      ref="patientPopup"
      :popupEventFlag="addPopupEventFlag"
      @popupClose="popupClose"
      :popupState="popupState"
      :subjectList="subjectList"
      :doctorList="doctorList"
      :centerList="centerList"
      :medicineList="medicineList"
      @refreshList="refreshPatientList"
    />
    <!-- <PatientModifyPopup
      :patientDetail="modifyPopupInfo"
      :eventFlag="modifyPopupEvntFlag"
      @modifyPopupClose="closeModifyPopup"
    /> -->
    <section class="patient__search__wrap box__shadow__01 vertical__scroll">
      <!-- NewRegistration component start -->
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["환자 조회"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["환자 조회"].subTitle }}
      </h5>
      <div class="patient__search__top">
        <ul class="checkbox__list">
          <li>
            <input
              type="checkbox"
              id="checkbox01"
              class="custom__checkbox"
              v-model="searchPrescriptionYn"
            />
            <label class="f__medium" for="checkbox01">처방전 미발행 포함</label>
          </li>
          <li>
            <input
              type="checkbox"
              id="checkbox02"
              class="custom__checkbox"
              v-model="searchCompleteYn"
            />
            <label class="f__medium" for="checkbox02">완치환자포함</label>
          </li>
        </ul>
      </div>
      <!-- NewRegistration component stop -->
      <!-- PatientSearch component start -->
      <div class="patient__search__condition__middle">
        <ul class="select__list">
          <li>
            <dl>
              <dt></dt>
              <dd>
                <select name="" id="" v-model="searchCenterId">
                  <option value="">전체 센터</option>
                  <option
                    v-for="center in centerList"
                    :key="center.index"
                    :value="center.codeId"
                  >
                    {{ center.codeName }}
                  </option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt></dt>
              <dd>
                <select name="" id="" v-model="searchSubjectId">
                  <option value="">전체 진료과목</option>
                  <option
                    v-for="subject in subjectList"
                    :key="subject.index"
                    :value="subject.codeId"
                  >
                    {{ subject.codeName }}
                  </option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt></dt>
              <dd>
                <select name="" id="" v-model="searchDoctorId">
                  <option value="">전체 담당의</option>
                  <option
                    v-for="doctor in doctorList"
                    :key="doctor.index"
                    :value="doctor.userId"
                  >
                    {{ doctor.userName }}
                  </option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt></dt>
              <dd>
                <input
                  class="custom__input"
                  type="text"
                  placeholder="환자명을 입력해주세요."
                  v-model.trim="searchPatientName"
                />
              </dd>
            </dl>
          </li>
          <li>
            <button class="custom__btn search__btn" @click="patientSearchClick">
              <span>
                <img
                  src="@/assets/images/icon/ic-search.svg"
                  alt="검색 아이콘"
                />
              </span>
              <p>검색</p>
            </button>
          </li>
        </ul>
        <!--<button
          class="custom__btn"
          v-if="toogleBtnByRole('FUNC_001')"
          @click="popupToggle('추가')"
        >-->
        <button class="custom__btn" @click="popupToggle('추가')">
          <span>
            <img src="@/assets/images/icon/ic-plus.svg" alt="플러스 아이콘" />
          </span>
          <p>환자추가</p>
        </button>
      </div>
      <!-- PatientSearch component stop -->
      <!-- @selectedRowObj="showDetail" -->
      <PatientListGird
        @modifyPopupOpen="showModifyPopup"
        :searchParams="searchParams"
        ref="patientGird"
        :triggerRefreshListFlag="triggerRefreshListFlag"
      />
    </section>
    <!-- <PatientDetail
      :patientDetail="selectedRowObj"
      :subjectList="subjectList"
      @mStrongPopupOpen="toggleMStrongPopup"
    /> -->
  </main>
</template>
<script>
import "@/assets/css/calendar.css";
import "@/assets/css/patientCare.css";
// import NewRegistration from "@/components/PatientInquiry/NewRegistration";
//import PatientSearch from "@/components/PatientInquiry/PatientSearch";
import PatientListGird from "@/components/PatientInquiry/PatientListGird";
// import PatientDetail from "@/components/PatientInquiry/PatientDetail_Back";
// import PatientModifyPopup from "@/components/PatientInquiry/PatientModifyPopup_Back";
import CommonPatientPopup from "@/components/PatientInquiry/CommonPatientPopup";
// import MStrongPopup from "@/components/PatientInquiry/MStrongPrescriptionPopup";
import { getSimpleUserInfo } from "@/utill/auth";
import data2 from "@/data/patientManage/data2.json";
import data3 from "@/data/patientManage/data3.json";
import data4 from "@/data/patientManage/data4.json";
import data5 from "@/data/patientManage/data5.json";

export default {
  data() {
    return {
      searchCenterId: "",
      searchSubjectId: "",
      searchDoctorId: "",
      searchPatientName: "",
      searchPrescriptionYn: true,
      searchCompleteYn: "",
      selectedRowObj: {},
      modifyPopupInfo: {},
      // modifyPopupEvntFlag: "Close",
      addPopupEventFlag: "Close",
      popupState: "",
      // mStrongPopupEventFlag: "Close",
      roleId: "",
      centerList: [],
      subjectList: [],
      doctorList: [],
      medicineList: [],
      triggerRefreshListFlag: false,
    };
  },
  computed: {
    searchParams() {
      return {
        searchCenterId: this.searchCenterId,
        searchSubjectId: this.searchSubjectId,
        searchDoctorId: this.searchDoctorId,
        searchPrescriptionYn: this.searchPrescriptionYn,
        searchCompleteYn: this.searchCompleteYn,
      };
    },
  },
  components: {
    //NewRegistration,
    //PatientSearch,
    PatientListGird,
    // PatientDetail,
    // PatientModifyPopup,
    CommonPatientPopup,
    // MStrongPopup,
  },
  created() {
    this.getCenterList();
    this.getSubjectList();
    this.getDoctorList();
    this.getMedicineList();
    const user = getSimpleUserInfo;
    const userId = user.userId;
    this.searchDoctorId = userId;
  },
  methods: {
    patientSearchClick() {
      this.$refs.patientGird.searchPatientClick(this.searchPatientName);
    },
    // showDetail(param) {
    //   this.selectedRowObj = param;
    // },
    showModifyPopup(param) {
      //this.modifyPopupEvntFlag = "Open";
      //this.modifyPopupInfo = param;
      this.selectedRowObj = param;
      this.popupToggle("수정");
      //
    },
    // toggleMStrongPopup() {
    //   this.mStrongPopupEventFlag === "Close"
    //     ? (this.mStrongPopupEventFlag = "Open")
    //     : (this.mStrongPopupEventFlag = "Close");
    // },
    closeModifyPopup(param) {
      this.modifyPopupEvntFlag = param;
    },
    toogleBtnByRole(funcId) {
      const simpleUserInfo = JSON.parse(getSimpleUserInfo());
      this.roleId = simpleUserInfo.roleId;
      if (this.roleId === "ROLE004" && funcId === "FUNC_001") {
        return true;
      } else {
        return false;
      }
    },
    popupToggle(evtName) {
      if (evtName === "수정") {
        this.popupState = "수정";
        this.$refs.patientPopup.popupInit("수정", this.selectedRowObj);
      } else {
        this.popupState = "추가";
        this.$refs.patientPopup.popupInit("추가", {});
      }
      this.addPopupEventFlag === "Close"
        ? (this.addPopupEventFlag = "Open")
        : (this.addPopupEventFlag = "Close");
    },
    popupClose() {
      this.addPopupEventFlag = "Close";
    },
    getCenterList() {
      this.centerList = data2;
    },
    getSubjectList() {
      this.subjectList = data4;
    },
    getDoctorList() {
      this.doctorList = data3;
    },
    getMedicineList() {
      this.medicineList = data5;
    },
    refreshPatientList() {
      this.triggerRefreshListFlag = !this.triggerRefreshListFlag;
    },
  },
};
</script>
