<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup add__patient__popup box__shadow__02">
      <div class="popup__top">
        <h4>환자 {{ popupState }}</h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <ul>
        <li>
          <dl>
            <dt class="f__medium">이름</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqPatientObj.patientName"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">성별</dt>
            <dd class="radio__list">
              <label>
                <input
                  type="radio"
                  name="sex"
                  value="M"
                  v-model="reqPatientObj.sex"
                />
                남자
              </label>
              <label>
                <input
                  type="radio"
                  name="sex"
                  value="F"
                  v-model="reqPatientObj.sex"
                />
                여자
              </label>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">생년월일</dt>
            <dd class="birthdate__list">
              <ul>
                <li>
                  <select name="" id="" v-model="birthdate_year">
                    <option value="">선택</option>
                    <option
                      :value="year + 1919"
                      v-for="year in new Date().getFullYear() - 1919"
                      :key="year.index"
                    >
                      {{ year + 1919 }}
                    </option>
                  </select>
                  년
                </li>
                <li>
                  <select name="" id="" v-model="birthdate_month">
                    <option value="">선택</option>
                    <option
                      :value="month"
                      v-for="month in 12"
                      :key="month.index"
                    >
                      {{ month }}
                    </option>
                  </select>
                  월
                </li>
                <li>
                  <select name="" id="" v-model="birthdate_day">
                    <option value="">선택</option>
                    <option :value="date" v-for="date in 31" :key="date.index">
                      {{ date }}
                    </option>
                  </select>
                  일
                </li>
              </ul>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">센터</dt>
            <dd>
              <select name="" id="" v-model="reqPatientObj.centerId">
                <option
                  v-for="center in centerList"
                  :key="center.index"
                  :value="center.codeId"
                >
                  {{ center.codeName }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">주소</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqPatientObj.address"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">담당의</dt>
            <dd>
              <select name="" id="" v-model="reqPatientObj.doctorId">
                <option
                  v-for="doctor in doctorList"
                  :key="doctor.index"
                  :value="doctor.userId"
                >
                  {{ doctor.userName }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">진료과목</dt>
            <dd>
              <select name="" id="" v-model="reqPatientObj.subjectId">
                <option
                  v-for="subject in subjectList"
                  :key="subject.index"
                  :value="subject.codeId"
                >
                  {{ subject.codeName }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">초진일</dt>
            <dd class="calendar__wrap top right">
              <input
                type="text"
                class="date__input"
                readonly
                v-model="firstTreatDateInput"
              />

              <span
                class="calendar__icon"
                id="calendar01"
                @click="calendarIconClick"
              >
                <img
                  src="@/assets/images/icon/ic-calendar.svg"
                  alt="달력 아이콘"
                />
              </span>
              <VDatePicker
                @input="clickEndDate($event)"
                locale="ko"
                color="gray"
                :class="{ active: calendarShow }"
                :value="firstTreatDateInput"
                v-model="firstTreatDateInput"
                v-on:focus="true"
                is-required
              />
              <!-- :value="educationStartDate" -->
            </dd>
          </dl>
        </li>
        <li class="weight__height__list">
          <dl>
            <dt class="f__medium">몸무게</dt>
            <dd>
              <input
                type="number"
                class="custom__input"
                v-model="reqPatientObj.weight"
              />
            </dd>
          </dl>
          <dl>
            <dt class="f__medium">키</dt>
            <dd>
              <input
                type="number"
                class="custom__input"
                v-model="reqPatientObj.height"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">연락처</dt>
            <dd>
              <input
                type="number"
                class="custom__input"
                v-model="reqPatientObj.contact"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">내복약</dt>
            <dd class="add__medicine">
              <select name="" id="" v-model="selectedMedicine">
                <option selected disabled>내복약을 선택하세요</option>
                <option
                  v-for="(medicine, index) in candidateMedicineList"
                  :key="index"
                >
                  {{ medicine.mediName }}
                </option>
              </select>
              <button class="custom__btn" @click="addMedicine()">
                <span>
                  <img
                    src="@/assets/images/icon/ic-plus.svg"
                    alt="추가 아이콘"
                  />
                </span>
                <p>추가</p>
              </button>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">이메일</dt>
            <dd>
              <input
                type="email"
                class="custom__input"
                v-model="reqPatientObj.email"
              />
            </dd>
          </dl>
        </li>
        <li class="add__medicine__list__wrap">
          <ul class="add__medicine__list vertical__scroll">
            <li v-for="(medicine, idx) in medications" :key="idx">
              <p>{{ medicine.mediName }}</p>
              <span
                class="remove__btn"
                @click="removeMedicine(medicine)"
              ></span>
            </li>
          </ul>
        </li>
        <li class="memo__list__wrap">
          <dl>
            <dt class="f__medium">메모</dt>
            <dd class="memo__list">
              <textarea
                name=""
                class="custom__input"
                v-model="reqPatientObj.memo"
              ></textarea>
            </dd>
          </dl>
        </li>
      </ul>
      <div class="button__wrap">
        <button class="custom__btn" @click="addOrUpdatePatient">
          <span v-if="popupState === '추가'">
            <img src="@/assets/images/icon/ic-plus.svg" alt="추가 아이콘" />
          </span>
          <p>{{ popupState }}</p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import VDatePicker from "v-calendar/lib/components/date-picker.umd";
import dateParse from "@/utill/dateParse.js";
import { clear } from "@/utill/commom";
import { getSimpleUserInfo } from "@/utill/auth";
import {
  checkTextLength,
  isNotEmpty,
  isNumber,
  isMobile,
} from "@/utill/validator";

export default {
  data() {
    return {
      calendarShow: false,
      reqPatientObj: {
        medications: [],
        patientName: "",
        sex: "M",
        firstTreatDate: "",
        birthday: "",
        doctorInCharge: "",
        doctorId: "",
        subjectId: "",
        weight: "",
        contact: "",
        memo: "",
        address: "",
        centerId: "",
        centerName: "",
        email: "",
        height: "",
      },
      birthdate_year: "",
      birthdate_month: "",
      birthdate_day: "",
      candidateMedicineList: [],
      selectedMedicine: "내복약을 선택하세요",
      medications: [],
      firstTreatDateInput: "",
      popupState: "",
      // medicineInput: "",
    };
  },
  props: {
    popupEventFlag: String,
    subjectList: Array,
    doctorList: Array,
    centerList: Array,
    medicineList: Array,
  },
  components: {
    VDatePicker,
  },
  methods: {
    popupInit(state, patientObj) {
      this.medicineReset();
      this.popupState = state;
      if (state === "수정") {
        const tempObj = patientObj;
        if (tempObj.sex === "남성") {
          tempObj.sex = "M";
        } else {
          tempObj.sex = "F";
        }
        tempObj.patientName = tempObj.name;
        if (tempObj.birthDay.length == 8) {
          this.birthdate_year = Number.parseInt(tempObj.birthDay.slice(0, 4));
          this.birthdate_month = Number.parseInt(tempObj.birthDay.slice(4, 6));
          this.birthdate_day = Number.parseInt(tempObj.birthDay.slice(6));
        }
        this.reqPatientObj = tempObj;
        this.firstTreatDateInputSet();
        this.medications = this.reqPatientObj.medications.slice();
      } else if (state === "추가") {
        // 추가 팝업 시 환자 데이터 리셋
        this.resetReqPatientObj();
      }
      this.calendarShow = false;
      this.selectedMedicine = "내복약을 선택하세요";
    },
    medicineReset() {
      this.candidateMedicineList = [];
      if (this.$env == "development") console.log(this.medicineList);
      for (let medicine of this.medicineList) {
        this.candidateMedicineList.push({
          mediId: medicine.codeId,
          mediName: medicine.codeName,
        });
      }
    },
    popupCloseClick() {
      /*this.reqPatientObj.medications = [];*/
      this.reqPatientObj.firstTreatDate = "";
      this.calendarShow = false;
      // this.resetReqPatientObj();
      this.$emit("popupClose", "Close");
      this.selectedMedicine = "내복약을 선택하세요";
    },
    calendarIconClick() {
      this.calendarShow === false
        ? (this.calendarShow = true)
        : (this.calendarShow = false);
    },
    clickEndDate(param) {
      if (param != null) {
        this.reqPatientObj.firstTreatDate = dateParse.dateToString2(param);
        this.firstTreatDateInputSet();
      }
      this.calendarShow = false;
    },
    addOrUpdatePatient() {
      this.reqPatientObj.medications = this.medications.slice();
      if (this.validatePatient()) {
        this.tranferDate();
        const paramObj = {};
        paramObj.centerId = this.reqPatientObj.centerId;
        paramObj.subjectId = this.reqPatientObj.subjectId;
        paramObj.doctorInCharge = this.reqPatientObj.doctorId;
        paramObj.patientName = this.reqPatientObj.patientName;
        paramObj.sex = this.reqPatientObj.sex;
        paramObj.birthday = this.reqPatientObj.birthday;
        paramObj.email = this.reqPatientObj.email;
        paramObj.contact = this.reqPatientObj.contact;
        paramObj.address = this.reqPatientObj.address;
        paramObj.firstTreatDate = this.reqPatientObj.firstTreatDate;
        paramObj.medications = this.reqPatientObj.medications;
        paramObj.weight = this.reqPatientObj.weight;
        paramObj.height = this.reqPatientObj.height;
        paramObj.memo = this.reqPatientObj.memo;

        if (this.$env == "development") console.log(paramObj);
        if (this.popupState === "추가") {
          //if (this.$env == "development") console.log(response);
        } else {
          //if (this.$env == "development") console.log(response);
        }
      }
    },
    tranferDate() {
      this.reqPatientObj.birthday = dateParse.dateToString(
        new Date(
          this.birthdate_year,
          this.birthdate_month - 1,
          this.birthdate_day
        )
      );
    },
    addMedicine() {
      if (this.selectedMedicine !== "내복약을 선택하세요") {
        const medicineFilter = this.candidateMedicineList.filter(
          (obj) => obj.mediName === this.selectedMedicine
        );
        const medicine = medicineFilter[0];

        this.medicineReset();
        let result = this.medications.find(function (object) {
          return object.mediId === medicine.mediId;
        });
        if (result === undefined || result === null) {
          this.medications.push(medicine);
        } else {
          this.$toast.error("해당 약이 이미 추가되었습니다.");
        }
      } else {
        this.$toast.error("내복약을 선택하세요.");
      }
    },
    firstTreatDateInputSet() {
      this.firstTreatDateInput = this.reqPatientObj.firstTreatDate.replace(
        /(\d{4})(\d{2})(\d{2})/,
        "$1-$2-$3"
      );
    },
    validatePatient() {
      if (!checkTextLength(this.reqPatientObj.patientName, 2, 20)) {
        this.$toast.error("환자명은 2~20자내 입력하세요.");
        return false;
      }
      if (!isNotEmpty(this.birthdate_year)) {
        this.$toast.error("년을 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.birthdate_month)) {
        this.$toast.error("월을 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.birthdate_day)) {
        this.$toast.error("일을 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqPatientObj.centerId)) {
        this.$toast.error("센터를 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqPatientObj.doctorInCharge)) {
        this.$toast.error("담당의를 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqPatientObj.subjectId)) {
        this.$toast.error("진료과목을 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqPatientObj.firstTreatDate)) {
        this.$toast.error("초진일을 선택하세요.");
        return false;
      }
      if (!isNotEmpty(this.reqPatientObj.weight)) {
        this.$toast.error("몸무게는 필수 입력 입니다.");
        return false;
      }
      if (!isNotEmpty(this.reqPatientObj.height)) {
        this.$toast.error("키는 필수 입력 입니다.");
        return false;
      }
      if (!checkTextLength(this.reqPatientObj.height + "", 2, 3)) {
        this.$toast.error("키는 2~3자리내 숫자를 입력하세요.");
        return false;
      }

      if (!isNumber(this.reqPatientObj.weight)) {
        this.$toast.error("몸무게는 숫자만 입력 가능합니다.");
        return false;
      }

      if (!checkTextLength(this.reqPatientObj.weight + "", 2, 3)) {
        this.$toast.error("몸무게는 2~3자리내 숫자를 입력하세요.");
        return false;
      }
      if (this.reqPatientObj.weight > 200) {
        this.$toast.error("몸무게는 200kg 이내로 입력하세요.");
        return false;
      }

      if (
        isNotEmpty(this.reqPatientObj.contact) &&
        !isMobile(this.reqPatientObj.contact)
      ) {
        this.$toast.error("-없이 올바른 핸드폰 번호를 입력하세요.");
        return false;
      }
      if (
        isNotEmpty(this.reqPatientObj.memo) &&
        !checkTextLength(this.reqPatientObj.memo, 0, 500)
      ) {
        this.$toast.error("500자이내 메모를 입력하세요.");
        return false;
      }
      return true;
    },
    resetReqPatientObj() {
      clear(this.reqPatientObj);
      this.reqPatientObj.sex = "M";
      this.birthdate_year = "";
      this.birthdate_month = "";
      this.birthdate_day = "";
      const simpleUserInfo = getSimpleUserInfo;
      this.medications = [];
      this.reqPatientObj.centerId = simpleUserInfo.centerId;
      this.reqPatientObj.doctorId = simpleUserInfo.userId;
      this.reqPatientObj.doctorInCharge = simpleUserInfo.userId;
      this.reqPatientObj.firstTreatDate = dateParse.dateToString2(new Date());
      this.firstTreatDateInputSet();
    },
    removeMedicine(medicine) {
      for (let i = 0; i < this.medications.length; i++) {
        if (
          medicine.mediId === this.medications[i].mediId &&
          medicine.mediName === this.medications[i].mediName
        ) {
          this.medications.splice(i, 1);
          i--;
        }
      }
    },
  },
  mounted() {
    this.medicineReset();
  },
};
</script>
