<template>
  <div class="grid__box">
    <LoadingBar v-if="isLoading" />
    <div
      class="patient__grid__bottom"
      @click="rowClick($event)"
      v-if="!isLoading"
    >
      <Grid
        ref="gridEl"
        :cols="cols"
        :class="{ on: selectedFlag }"
        :sort="true"
        :pagination="true"
        :autoWidth="true"
        :rows="getPatientList"
      ></Grid>
    </div>
  </div>
</template>
<script>
import Grid from "gridjs-vue";
import LoadingBar from "@/components/common/LoadingBar";
import { getSimpleUserInfo } from "@/utill/auth";
import { fn_postSubmit } from "@/utill/commom";
import data1 from "@/data/patientManage/data1.json";
import data5 from "@/data/dashboard/astop/data5.json";

export default {
  components: {
    Grid,
    LoadingBar,
  },
  props: {
    searchParams: Object,
    triggerRefreshListFlag: Boolean,
  },
  watch: {
    searchParams() {
      this.createSearchParamsString();
      this.getPatients(this.searchParamsString);
      /*if (this.searchParams.searchPatientName !== "") {
        this.patientList = this.patientListCache.filter((obj) =>
          obj.name.includes(this.searchParams.searchPatientName)
        );
      } else {
        this.patientList = this.patientListCache;
      }*/
      // this.getPatients(this.searchParamsString);
    },
    triggerRefreshListFlag() {
      this.getPatients(this.searchParamsString);
    },
  },
  data() {
    return {
      isLoading: false,
      searchPatientName: "",
      searchParamsString: "",
      cols: [
        { name: "", id: "accessCode", hidden: true },
        { name: "", id: "issuedYn", hidden: true },
        {
          name: "치료시작일",
          id: "startDate",
          formatter: (cell) => {
            if (cell !== null) {
              return `${cell.replace(/(\d{4})(\d{2})(\d{2})/, "$1-$2-$3")}`;
            } else {
              return "-";
            }
          },
        },
        { name: "병록번호", id: "patientRegNumber" },
        { name: "이름", id: "name" },
        { name: "진료과목", id: "subjectName" },
        { name: "성별", id: "sex" },
        { name: "나이", id: "age" },
        {
          name: "초재진",
          id: "diagnosisCount",
          formatter: (cell) => {
            let str = "";
            if (cell === 0 || cell === 1) {
              str = "초진";
            } else {
              str = "재진";
            }
            return str;
          },
        },
        { name: "담당의", id: "doctorInCharge" },
        { name: "연락처", id: "contact" },
        {
          name: "처방전",
          id: "",
          formatter: (cell, row) => {
            // if (this.$env == "development") console.log(cell);
            const accessCode = row._cells[0].data;
            const issuedYn = row._cells[1].data;
            if (
              accessCode === null ||
              accessCode === "" ||
              accessCode === undefined
            ) {
              return "-";
            } else {
              if (issuedYn === "N") {
                return "편집";
              } else if (issuedYn === "Y") {
                return "발행";
              }
            }
          },
        },
        { name: "앱버전", id: "appVersion" },
        //this.flagColumnObj(),
        // {
        //   name: "처방이력",
        //   formatter: () => {
        //     return this.$gridjs.h("button", {
        //       onClick: () => {
        //         this.$confirm({
        //           // title: "",
        //           message: "개발중입니다.",
        //           button: {
        //             yes: "확인",
        //           },
        //         });
        //       },
        //     });
        //   },
        // },
        // 처방이력 추후개발예정
        {
          name: "수정",
          formatter: (cell, row) => {
            return this.$gridjs.h("button", {
              onClick: () => {
                const selectedPatientRegNumber = row.cells[3].data;
                this.patientList.forEach((patient) => {
                  if (patient.patientRegNumber === selectedPatientRegNumber) {
                    this.selectedRowObj = patient;
                  }
                });
                this.modifyClick();
              },
            });
          },
          attributes: {
            className: "gridjs-th gridjs-th-sort gridjs-td modifyBtn",
          },
        },
        {
          name: "삭제",
          formatter: (cell, row) => {
            return this.$gridjs.h("button", {
              onClick: () => {
                const selectedPatientRegNumber = row.cells[3].data;
                this.patientList.forEach((patient) => {
                  if (patient.patientRegNumber === selectedPatientRegNumber) {
                    this.selectedRowObj = patient;
                  }
                });
                this.removePatient();
              },
            });
          },
          attributes: {
            className: "gridjs-th gridjs-th-sort gridjs-td deleteBtn",
          },
        },
      ],
      selectedRowObj: "",
      className: "",
      patientList: [],
      patientListCache: [],
      selectedFlag: false,
    };
  },
  computed: {
    getPatientList() {
      return this.patientList;
    },
  },
  created() {
    this.createSearchParamsString();
    this.getPatients(this.searchParamsString);
  },
  methods: {
    /*flagColumnObj() {
      const simpleUserInfo = JSON.parse(getSimpleUserInfo());
      //let hiddenFlag = simpleUserInfo.roleId === "ROLE003" ? false : true;
      let hiddenFlag;
      if (
        simpleUserInfo.roleId === "ROLE001" ||
        simpleUserInfo.roleId === "ROLE003"
      ) {
        hiddenFlag = false;
      } else hiddenFlag = true;

      return {
        hidden: hiddenFlag,
        name: "임상",
        id: "flag",
        formatter: (cell, row) => {
          const flag = row._cells[13].data;
          let data = "";
          if (flag === 0) {
            data = "비교군";
          } else if (flag === 1) {
            data = "시험군";
          }
          return data;
        },
      };
    },*/
    searchPatientClick(searchPatientName) {
      this.searchPatientName = searchPatientName;
      this.createSearchParamsString();
      this.getPatients(this.searchParamsString);
    },
    rowClick(params) {
      // const cellField = params.path[0].getAttribute("data-column-id");
      const cellField = params.srcElement.dataset.columnId; // 크롬 최신버전 이벤트 객체
      if (cellField !== null && cellField !== undefined) {
        // const obj = params.path[0].parentNode;
        const obj = params.srcElement.parentNode; // 크롬 최신버전 이벤트 객체
        this.jsonParse(obj);
        if (this.selectedRowObj.subjectId !== "APP003") {
          this.$router.push({
            name: "PatientDashboard",
            params: data5,
          });
        } else {
          const baseLink =
            this.$store.getters.getUserMenuTree[0].subMenu[2].link;
          const simpleUserInfo = JSON.parse(getSimpleUserInfo());
          const params = {
            mid: "training",
            accessKey: simpleUserInfo.accessToken,
            loginId: simpleUserInfo.userId,
            loginName: simpleUserInfo.name,
            patientNumber: this.selectedRowObj.patientRegNumber,
          };
          fn_postSubmit(baseLink, params);
        }
      }
      // if (this.className === "gridjs-td") {
      //   this.selectedFlag = true;
      //   this.selectedRowObj = "";
      //   if (params.path[0].parentNode.childNodes.length !== 1) {
      //     this.jsonParse(params.path[0].parentNode);
      //     params.path[0].parentNode.classList.add("on");
      //   }
      // } else {
      //   this.selectedFlag = false;
      //   this.selectedRowObj = "";
      // }
      // this.$emit("selectedRowObj", this.selectedRowObj);
      // if (this.$env == "development") console.log(this.className);
    },
    jsonParse(selectedRowObj) {
      const selectedPatientRegNumber = selectedRowObj.childNodes[1].textContent;
      this.patientList.forEach((patient) => {
        if (patient.patientRegNumber === selectedPatientRegNumber) {
          this.selectedRowObj = patient;
        }
      });
    },
    createSearchParamsString() {
      if (this.searchParams != null && this.searchParams !== "") {
        let patientName = this.searchPatientName;
        let centerId = this.searchParams.searchCenterId;
        let subjectId = this.searchParams.searchSubjectId;
        let doctorInCharge = this.searchParams.searchDoctorId;
        let complete = "";
        if (this.searchParams.searchCompleteYn) {
          complete = "Y";
        } else {
          complete = "N";
        }
        let prescription = "";
        if (this.searchParams.searchPrescriptionYn) {
          prescription = "";
        } else {
          prescription = "Y";
        }

        this.searchParamsString = `?doctorInCharge=${doctorInCharge}&centerId=${centerId}&subjectId=${subjectId}&patientName=${patientName}&prescription=${prescription}&complete=${complete}`;
      }
    },
    modifyClick() {
      if (typeof this.selectedRowObj === "object") {
        if (this.$env == "development") console.log(this.selectedRowObj);
        const cloneObj = {};
        for (let key in this.selectedRowObj) {
          cloneObj[key] = this.selectedRowObj[key];
        }
        this.$emit("modifyPopupOpen", cloneObj);
      } else {
        this.$toast.error("리스트에서 환자를 선택해주세요.");
      }
    },
    getPatients() {
      // if (this.$env == "development") console.log(searchParams);
      this.isLoading = true;
      // if (searchParams == null || searchParams === "") {
      //   const user = getSimpleUserInfo();
      //   const userId = JSON.parse(user).userId;
      //   searchParams = `?doctorInCharge=${userId}&complete="Y"`;
      // }

      this.patientList = data1;
      console.log(data1);
      this.patientList.forEach((patient) => {
        if (patient.sex === "M") {
          patient.sex = "남성";
        } else {
          patient.sex = "여성";
        }
        if (patient.birthDay.length == 8) {
          patient.age =
            Number.parseInt(new Date().getFullYear()) -
            Number.parseInt(patient.birthDay.slice(0, 4)) +
            1;
        }
      });
      this.patientListCache = this.patientList;
      this.selectedFlag = false;
      this.selectedRowObj = "";
      this.$emit("selectedRowObj", this.selectedRowObj);
      this.isLoading = false;
    },
    removePatient() {
      if (this.selectedRowObj != null || this.selectedRowObj !== "") {
        this.$confirm({
          // showCancelButton: true,
          title: "",
          message: "정말로 삭제 하시겠습니까?",
          button: {
            no: "아니오",
            yes: "네",
          },
          callback: (confirm) => {
            if (confirm) {
              //
            }
          },
        });
      } else {
        this.$toast.error("삭제할 환자를 선택해주세요.");
      }
    },
  },
};
</script>
